<template>
  <VanPopup
    style="box-sizing: border-box; background-color: transparent"
    round
    v-model="show"
    :close-on-click-overlay="false"
  >
    <div class="share-wrap">
      <div class="close-wrap">
        <div class="close-icon" @click="closePop">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-guanbixiao1"></use>
          </svg>
        </div>
      </div>
      <div class="title" v-if="type === 0">{{ $t('home.shop') }}</div>
      <div class="title" v-if="type === 1">{{ $t('home.exchangeFood') }}</div>
      <div class="food-wrap" translate="no">
        <FoodItem
          class="item"
          v-for="item in foodList"
          :key="item.id"
          :dataSource="item"
          :type="type"
          @show="handleDetailShow"
          :tokenMap="tokenMap"
        ></FoodItem>
        <div class="none-data" v-if="foodList.length < 1">
          {{ $t('common.noHave') }}
        </div>
      </div>
    </div>
    <LightPop v-if="lightPopShow" @close="handleLightPop" :close-icon="true">
      <div class="pop-wrap">
        <div class="des1" v-if="lang === 'ZH'">
          {{ tempData.json.contentZH }}
        </div>
        <div class="des1" v-if="lang === 'EN'">
          {{ tempData.json.contentEN }}
        </div>
        <div class="des2">
          {{ $t('explore.canAdd') }}<span> {{ tempData.energy }} </span
          >{{ $t('explore.satiety') }}
        </div>
        <div class="select-wrap" @click="handleBuyFood">
          {{ $t('explore.exchange') }}
        </div>
      </div>
    </LightPop>
  </VanPopup>
</template>

<script>
import { handleFoodList, handleBuyFood } from '@/services/shop'
import LightPop from '@/components/LightPop.vue'
import FoodItem from './FoodItem.vue'
import { mapState } from 'vuex'

export default {
  props: {
    type: {
      type: Number,
      default: 0 // 0 商店 1 兑换食物
    }
  },
  name: '',
  data () {
    return {
      lightPopShow: false,
      show: true,
      foodList: [],
      tempData: {},
      tokenMap: {}
    }
  },
  computed: {
    ...mapState(['lang'])
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    handleLightPop () {
      this.lightPopShow = !this.lightPopShow
    },
    handleDetailShow (dataSource) {
      this.handleLightPop()
      this.tempData = dataSource
    },
    async getFoodList () {
      const resp = await handleFoodList()

      if (!resp.success) return
      this.foodList = resp.rows
      this.foodList.forEach(item => {
        item.json = JSON.parse(item.json)
        if (item.tokenType > -1) {
          item.tokenName = this.tokenMap[item.tokenType]
        }
      })
    },
    async handleBuyFood () {
      const resp = await handleBuyFood({
        id: this.tempData.id,
        number: 1,
        payType: this.type
      })
      if (!resp.success) return
      this.$parent.getHomeInfo()
      this.handleLightPop()
      this.$toast('success')
    }
  },
  mounted () {
    this.getFoodList()
    this.tokenMap = JSON.parse(localStorage.getItem('tokenList'))
    console.log('this.tokenMap', this.tokenMap)
  },
  components: { LightPop, FoodItem }
}
</script>

