<template>
  <VanPopup
    style="box-sizing: border-box; background-color: transparent"
    round
    v-model="show"
    :close-on-click-overlay="false"
  >
    <div class="explore-wrap">
      <div class="close-wrap">
        <div class="close-icon" @click="handleShowNotice">
          <svg class="icon1" aria-hidden="true">
            <use xlink:href="#jieshao"></use>
          </svg>
        </div>
        <div class="close-icon" @click="closePop">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-guanbixiao1"></use>
          </svg>
        </div>
      </div>
      <div class="title">{{ $t('explore.explore') }}</div>
      <div class="food-take-wrap" translate="no" v-if="!selectShow">
        <div class="take-item" v-for="item in 5" :key="item">
          <TakeFoodItem
            :index="item"
            :dataSource="takeFoodList[item]"
            @show="handleSelect"
          ></TakeFoodItem>
        </div>
      </div>
      <div class="food-wrap" translate="no" v-else>
        <div class="food-item" v-for="(item, index) in foodList" :key="item.id">
          <div class="select-wrap">
            <div class="name-wrap">
              <div class="name" v-if="lang === 'ZH'">
                {{ item.json.nameZH }}
              </div>
              <div class="name" v-if="lang === 'EN'">
                {{ item.json.nameEN }}
              </div>
              <div class="price">
                <span class="num-name">{{ $t('common.num') }}</span>
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#star2"></use>
                </svg>
                <span class="num">{{ item.amount }}</span>
              </div>
            </div>
            <div class="image">
              <img :src="item.image" alt="" />
            </div>
            <VanButton
              class="select-btn"
              @click="handleLightPop(index)"
              color="#f8bb5a"
              :disabled="item.amount === 0"
              >{{ $t('explore.select') }}</VanButton
            >
          </div>
        </div>
        <div class="none-data" v-if="foodList.length < 1">
          {{ $t('common.noHave') }}
        </div>
      </div>
    </div>
    <div class="button-group">
      <div class="btn1" @click.stop="closePop">{{ $t('common.cancel') }}</div>
      <div class="btn2" @click.stop="handleGo">{{ $t('explore.go') }}</div>
    </div>
    <NoticePop v-if="noticeShow" @close="noSatietyShow"></NoticePop>
    <NoSatietyPop
      v-if="noSatietyShow"
      @close="handleNoSatietyShow"
    ></NoSatietyPop>
    <LightPop v-if="lightPopShow" @close="handleLightPop" :close-icon="true">
      <div class="pop-wrap">
        <div class="des1">{{ foodList[currentIndex].introduce }}</div>
        <div class="des2">
          {{ $t('explore.canAdd')
          }}<span> {{ foodList[currentIndex].energy }} </span
          >{{ $t('explore.satiety') }}
        </div>
        <div class="select-wrap" @click="handleSelectFood">
          {{ $t('explore.exchange') }}
        </div>
      </div>
    </LightPop>
  </VanPopup>
</template>

<script>
import { mapState } from 'vuex'

import { handleStartExplore } from '@/services/explore'
import LightPop from '../../components/LightPop.vue'
import NoSatietyPop from './components/NoSatietyPop.vue'
import NoticePop from './components/NoticePop.vue'
import { handleFoodList } from '@/services/food'
import TakeFoodItem from './components/TakeFoodItem.vue'
export default {
  components: { NoticePop, NoSatietyPop, LightPop, TakeFoodItem },
  name: '',
  data () {
    return {
      show: true,
      foodShow: false,
      noticeShow: false,
      noSatietyShow: false,
      lightPopShow: false,
      selectShow: false,
      currentIndex: -1,
      selectIndex: -1,
      foodList: [],
      takeFoodList: []
    }
  },
  computed: {
    ...mapState(['lang'])
  },
  methods: {
    async getList () {
      const resp = await handleFoodList()
      if (!resp.success) return
      this.foodList = resp.rows
      this.foodList.forEach(item => {
        item.json = JSON.parse(item.json)
      })
    },
    handleShowNotice () {
      this.noticeShow = !this.noticeShow
    },
    handleNoSatietyShow () {
      this.noSatietyShow = !this.noSatietyShow
    },
    handleLightPop (index) {
      this.lightPopShow = !this.lightPopShow
      this.currentIndex = index
    },
    handleSelect (index) {
      this.selectIndex = index
      this.selectShow = !this.selectShow
    },
    handleSelectFood () {
      this.takeFoodList[this.selectIndex] = {
        amount: 1,
        name: this.foodList[this.currentIndex].name,
        image: this.foodList[this.currentIndex].image,
        id: this.foodList[this.currentIndex].id,
        foodId: this.foodList[this.currentIndex].foodId,
        json: this.foodList[this.currentIndex].json
      }
      this.foodList[this.currentIndex].amount = this.foodList[this.currentIndex].amount - 1
      this.handleLightPop()
      this.selectShow = false
    },
    closePop () {
      if (this.selectShow) {
        this.selectShow = false
      } else {
        this.$emit('close')
      }
    },
    async handleGo () {
      const list = []
      this.takeFoodList.forEach(item => {
        list.push({
          foodId: item.foodId,
          amount: item.amount
        })
      })
      const resp = await handleStartExplore({
        list
      })
      if (!resp.success) return
      this.$emit('travelStart')
      // this.$emit('close')
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

