<template>
  <VanPopup
    style="box-sizing: border-box; background-color: transparent"
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="ignore" translate="no">
      <div class="share-wrap">
        <div class="header ignore">
          <div class="reward">{{$t('home.reward')}}</div>
          <div class="satiety">({{$t('explore.satiety')}})</div>
          <div class="cvr">{{ shareInfo.totalAmount }}</div>
          <div class="friends-num">
            {{$t('home.friendNum')}} {{ shareInfo.friendsNumber }}
          </div>
        </div>
        <div class="key-value">
          <div class="key">
            {{$t('home.rewardBalance')}}
            <div class="des">({{$t('explore.satiety')}}）:</div>
            {{ shareInfo.remainingAmount }}
          </div>
          <div class="chain"></div>
          <div class="value" @click="handleToShop">{{$t('home.exchangeFood')}}</div>
        </div>
        <div class="key-value">
          <div class="key">
            {{$t('home.inviteCode')}}
            <div class="invite-code">{{ shareInfo.inviteCode }}</div>
          </div>
          <div class="chain"></div>
          <div class="value" @click="handleCopy(shareInfo.inviteCode)">
            {{$t('common.copy')}}
          </div>
        </div>
        <div class="key-value">
          <div class="key">
            <div class="invite-name">{{$t('home.inviteLink')}}</div>
            <div class="link">
              {{ inviteLink }}
            </div>
          </div>
          <div class="chain"></div>
          <div class="value" @click="handleCopy(inviteLink)">{{$t('common.copy')}}</div>
        </div>
        <div class="bottom">
          <div class="star-main" @click="$gbUtils.handleToPage('/bag')">
            <div class="body">
              <img src="../../../assets/star/main/main2.png" alt="" />
            </div>
          </div>
          <div class="rule">
            <div class="name">{{$t('home.shareRule')}}</div>
            <div class="rule-item">
              {{$t('home.rule1')}}
            </div>
            <div class="rule-item">{{$t('home.rule2')}}</div>
          </div>
        </div>
      </div>
    </div>
  </VanPopup>
</template>

<script>
// import config from '../../../config/app.config'
import { handleSharePage } from '@/services/home'
export default {
  name: '',
  data () {
    return {
      show: true,
      shareInfo: {},
      inviteLink: ''
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    handleToShop () {
      this.$emit('showShop', 1)
    },
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    },
    async getInfo () {
      const resp = await handleSharePage()
      if (!resp.success) return
      this.shareInfo = resp.data
      this.inviteLink = location.host + '/#/?inviteCode=' + this.shareInfo.inviteCode
    }
  },
  mounted () {
    this.getInfo()
  }
}
</script>

