import request from '@/utils/request'

// 出行状态
export const handleExploreStatus = (params) => {
  return request('GET', '/dapp/travelLog/index', params)
}

// 出行信息
export const handleExploreInfo = (params) => {
  return request('GET', '/dapp/travelLog/travelInfo', params, true, true, 1)
}

// 开始出行
export const handleStartExplore = (params) => {
  return request('POST', '/dapp/travelLog/start', params, true, true, 0)
}

// 结束出行
export const handleEndExplore = (params) => {
  return request('POST', '/dapp/travelLog/end', params)
}
