<template>
  <VanPopup
    style="box-sizing: border-box; background-color: transparent"
    round
    v-model="show"
  >
    {{ travelInfo.isPop }}
    <!-- 正在探险中 -->
    <div
      class="explore-status-wrap"
      v-if="exploreInfo.isTraveling && !exploreInfo.travelTime"
    >
      <div class="content">
        <div class="title">
          {{ startExploreFlag ? $t('explore.went') : $t('explore.exploring') }}
        </div>
        <div class="text">
          {{ $t('explore.going')
          }}
          <!-- <span translate="no">
            {{ getTime(goInfo.duration * 60) || 0 }}
            {{ getUnit(goInfo.duration * 60) }}</span
          > -->
           <span translate="no">
             {{ timeStamp(travelInfo.duration || 0) }}
           </span>
          {{ $t('explore.goodMessage') }}
        </div>
        <!-- <div class="notice">{{ $t('explore.notice') }}</div> -->
      </div>
    </div>
    <div class="explore-status-wrap" v-else>
      <div class="content">
        <div class="title">{{ $t('explore.explored') }}</div>
        <div class="text">
          {{ $t('explore.exploredSuccess')
          }}<span translate="no">
            {{ getTime(exploreInfo.travelTime * 60) || 0
            }}{{ getUnit(exploreInfo.travelTime * 60) }}
          </span>
        </div>
        <div class="text">
          {{ $t('explore.consume') }}
          <span translate="no">
            {{ $t('explore.satiety') }}*{{ exploreInfo.satiety || 0 }}</span
          >
          <span v-for="item in exploreInfo.foodList" :key="item.name"
            >、{{ item.name }}*{{ item.amount }}</span
          >
        </div>
        <div class="text">
          {{ $t('explore.get') }}
          <span translate="no"
            >{{ $t('explore.experience')
            }}{{ exploreInfo.experience || 0 }}</span
          >
          <span translate="no"
            >、{{ $t('explore.fragments')
            }}{{ exploreInfo.fragments || 0 }}</span
          >
        </div>
      </div>
    </div>
    <div class="button" @click="closePop">{{ $t('common.ok') }}</div>
  </VanPopup>
</template>

<script>
import { handleExploreInfo, handleEndExplore, handleExploreStatus } from '@/services/explore'
export default {
  name: '',
  props: ['travelInfo', 'exploreInfo', 'startExploreFlag'],
  data () { // isPop 判断 0未读 1已读 已读状态下打开探险应该为添加食物页面
    return {
      show: true,
      // exploreInfo: {},
      validTime: 0,
      travelTime: 0,
      goInfo: {}
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
      this.$emit('changeStartFlag')
    },
    // 获取出行信息
    async getInfo () {
      const resp = await handleExploreInfo()
      if (!resp.success) return
      this.exploreInfo = resp.data
      this.exploreInfo.estimatedEndTime = this.$moment(this.exploreInfo.estimatedEndTime).valueOf()
      const nowTime = this.$moment().format('x')
      this.validTime = (this.exploreInfo.estimatedEndTime - nowTime) / 1000
      if (this.validTime < 0) {
        this.travelTime = (this.exploreInfo.estimatedEndTime - this.$moment(this.exploreInfo.startTime).valueOf()) / 1000
      }
    },
    async getGoInfo () {
      const resp = await handleExploreStatus()
      if (!resp.success) return
      this.goInfo = resp.data
    },
    async handleEndTravel () {
      const resp = await handleEndExplore()
      console.log('resp', resp)
    },
    // 计算分钟,小时,天
    getTime (value) {
      if (!value) return 0
      if (+value >= 3600) {
        return (+value / 3600).toFixed(0)
      } else {
        return (+value / 60).toFixed(0)
      }
    },
    // 获取天小时单位
    getUnit (value) {
      if (+value >= 3600) {
        return this.$t('common.hour')
      } else {
        return this.$t('common.min')
      }
    },
    timeStamp (StatusMinute) {
      var day = parseInt(StatusMinute / 60 / 24)
      var hour = parseInt((StatusMinute / 60) % 24)
      var min = parseInt(StatusMinute % 60)
      StatusMinute = ''
      if (day > 0) {
        StatusMinute = day + this.$t('common.day')
      }
      if (hour > 0) {
        StatusMinute += hour + this.$t('common.hour')
      }
      if (min >= 0) {
        StatusMinute += parseFloat(min) + this.$t('common.min')
      }
      return StatusMinute
    }
  },
  mounted () {
    this.getGoInfo()
    // this.getInfo()
  }
}
</script>

