<template>
  <div class="header-wrapper" translate="no">
    <div class="header">
      <div class="address">
        {{ $gbUtils.formatAddress(myAccount) }}
      </div>
      <div class="satiety" @click="handleShowSatiety">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#satiate"></use>
        </svg>
        <div class="value" translate="no">
          {{ parseInt(myInfo.satiety) || 0 }}/{{ fullSatiety }}
        </div>
        <svg class="add" aria-hidden="true">
          <use xlink:href="#jiahao11"></use>
        </svg>
      </div>
      <div class="balance" @click="$gbUtils.handleToPage('/warehourse')">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#mn1"></use>
        </svg>
        <VanLoading v-if="balanceLoading" size="20"></VanLoading>
        <div class="value" translate="no" v-else>
          {{ $gbUtils.formatTwoBalance(myBalance) }}
        </div>
      </div>
      <div class="menu iconfont manu" @click="changeMenuShow">
        <div class="dot" v-if="myInfo.unreadCount"></div>
      </div>
    </div>
    <!-- 菜单 -->
    <VanOverlay
      :show="menuShow"
      @click="changeMenuShow"
      :class-name="'overlayClass'"
    >
      <div class="menu-wrapper" id="menu-wrapper" v-if="menuShow">
        <div class="menu-top">
          <div
            :class="{ 'icon-box': item.showType > myInfo.type }"
            v-for="item in menuLink"
            :key="item.id"
            @click="handleMenuTop(item.id)"
          >
            <template v-if="item.showType > myInfo.type">
              <div :class="`iconfont ${item.icon} icon-${item.id}`">
                <div
                  class="dot"
                  :class="{ 'dot-en': lang === 'EN' }"
                  v-if="item.id === 1 && myInfo.unreadCount"
                ></div>
              </div>
              <div class="icon-text">{{ item.text }}</div>
            </template>
          </div>
        </div>
        <div class="menu-list">
          <div
            class="menu-item"
            v-for="item in menuList"
            :key="item.id"
            @click="handleMenu(item)"
          >
            <div class="menu-left felx">
              <span class="icon" :class="`iconfont ${item.icon}`"></span>
              <span class="icon-text">{{ item.text }}</span>
            </div>
            <div class="right-arrow iconfont a-youjiantoubeifen4"></div>
          </div>
        </div>
        <div class="more-tips"></div>
        <div class="more-tips">
          Ver1.0 Beta
          <div>{{ $t('menu.more') }}</div>
        </div>
      </div>
    </VanOverlay>
  </div>
</template>

<script>
import routerObj from '@/contracts/contractRouter'
import { handleMining } from '@/services/warehourse'
import { mapState } from 'vuex'
// import config from '@/config/app.config'
export default {
  name: 'Header',
  props: ['myAccount', 'myInfo'],
  data () {
    return {
      fullSatiety: 100,
      menuShow: false,
      balanceLoading: true,
      menuLink: [
        {
          id: 1,
          text: this.$t('menu.news'),
          textEn: 'Information',
          icon: 'a-youjianxinfengxinxiduanxin',
          showType: 5
        },
        {
          id: 2,
          text: this.$t('menu.wallet'),
          textEn: 'Wallet',
          icon: 'a-bianzu7',
          showType: 5
        },
        // {
        //   id: 2,
        //   text: 'SWAP',
        //   textEn: 'SWAP',
        //   icon: 'SWAP'
        // },
        {
          id: 3,
          text: this.$t('menu.share'),
          textEn: 'Share',
          icon: 'fenxiang',
          showType: 2
        }
      ],
      mediaMenu: [
        {
          id: 1,
          text: this.$t('menu.duomeiti'),
          icon: 'duomeiti',
          url: ''
        }
      ],
      institutionMenu: [
        {
          id: 2,
          text: this.$t('menu.yanjiujigou'),
          icon: 'yanjiujigou',
          url: ''
        }
      ],
      kolMenu: [
        {
          id: 3,
          text: this.$t('menu.tuanduiguanli'),
          icon: 'tuanduiguanli',
          url: '',
          needNft: true
        }
      ],
      menuList: [],
      defaultMenuList: [
        {
          id: 4,
          text: this.$t('menu.language'),
          icon: 'yuyan',
          url: ''
        },
        // {
        //   id: 5,
        //   text: '打开音效',
        //   icon: 'shengyinlijie',
        //   url: ''
        // }
        // {
        //   id: 6,
        //   text: this.$t('menu.kol'),
        //   icon: 'duomeiti',
        //   url: '/preSaleKol'
        // },
        // {
        //   id: 7,
        //   text: 'NFT预售',
        //   icon: 'duomeiti',
        //   url: '/preSaleNormal'
        // }
        {
          id: 8,
          text: this.$t('menu.emial'),
          icon: 'youxiang'
        },
        {
          id: 9,
          text: this.$t('home.t'),
          icon: 'telegram-plane'
        },
        {
          id: 10,
          text: this.$t('home.tutorial'),
          icon: 'book-full',
          url: '/tutorial'
        },
        {
          id: 12,
          text: this.$t('menu.feature'),
          icon: 'guihua',
          url: '/plan'
        },
        {
          id: 11,
          text: this.$t('menu.paper'),
          icon: 'baogao',
          url: ''
        }
        // {
        //   id: 13,
        //   text: 'StarFish DAO',
        //   icon: 'zhili',
        //   url: ''
        // }
      ],
      musicFlag: false,
      myBalance: 0,
      telegrame: 'https://t.me/StarFishOs',
      email: 'service@sfos.io'
    }
  },
  watch: {
    musicFlag (val) {
      this.menuList[4].text = val ? '关闭音效' : '打开音效'
    },
    myInfo (val) {
      console.log('val', val)
      this.setMenu()
      this.setFullSatiety()
    }
  },
  methods: {
    init () {
      this.setMenu()
      this.setFullSatiety()
      this.getMining()
    },
    changeMenuShow () {
      this.menuShow = !this.menuShow
    },
    handleToPage (path) {
      // if (!this.myInfo.level) return
      this.$gbUtils.handleToPage(path)
    },
    handleMenuTop (key) {
      switch (key) {
        case 1:
          this.$gbUtils.handleToPage('/information')
          break
        case 2:
          // this.$toast(this.$t('common.notOpen'))
          this.$gbUtils.handleToPage('/warehourse')
          break
        case 3:
          if (!this.myInfo.level) return
          if (this.myInfo.type === 1) {
            return this.$gbUtils.handleToPage('/teamLeader')
          }
          this.$emit('show', 'share')
          break
      }
    },
    setFullSatiety () {
      this.fullSatiety = this.$gbUtils.getFullSatiety(this.myInfo)
    },
    setMenu () {
      // 卡类型 0普通 1团长 2机构 3媒体
      switch (this.myInfo.type) {
        case 1:
          this.menuList = this.kolMenu.concat(this.defaultMenuList)
          break
        case 2:
          this.menuList = this.institutionMenu.concat(this.defaultMenuList)
          break
        case 3:
          this.menuList = this.mediaMenu.concat(this.defaultMenuList)
          break
        default:
          this.menuList = this.defaultMenuList
      }
    },
    handleMenu (item) {
      if (item.url) {
        if (item.needNft && !this.myInfo.level) return
        this.$gbUtils.handleToPage(item.url)
      }
      const key = item.id
      switch (key) {
        case 1:
          if (!this.myInfo.level) return
          this.$gbUtils.handleToPage('/mediaAdmin')
          break
        case 2:
          if (!this.myInfo.level) return
          this.$gbUtils.handleToPage('/agency')
          break
        case 3:
          if (!this.myInfo.level) return
          this.$gbUtils.handleToPage('/teamLeader')
          break
        case 4:
          this.$emit('show', 'lang')
          break
        case 5:
          this.musicFlag = !this.musicFlag
          this.$emit('musicControl', this.musicFlag)
          this.changeMenuShow()
          break
        case 8:
          this.handleCopy(this.email)
          break
        case 9:
          window.open(this.telegrame)
          break
        case 11:
          window.open('https://www.certik.com/projects/starfish-os')
          break
        case 13:
          window.open('https://dao.sfos.io/')
          break
      }
    },
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    },
    handleShowSatiety () {
      if (!this.myInfo.level) return
      this.$emit('show', 'satiety')
    },
    async getMining () {
      this.myBalance = 0
      const resp = await handleMining()
      if (!resp.success) return
      this.miningInfo = resp.data
      const tokenList = {}
      resp.data.supportTokenList.forEach((item) => {
        tokenList[item.tokenType] = item.tokenName
      })
      localStorage.setItem('tokenList', JSON.stringify(tokenList))
      this.handleTotalAmount()
      this.balanceLoading = false
    },
    async handleTotalAmount () {
      const tokenPrice = {}
      this.myBalance = 0
      for (let i = 0; i < this.miningInfo.assetsList.length; i++) {
        const resp = await routerObj.getAmountsIn(
          this.miningInfo.assetsList[i].token
        )
        console.log('+this.miningInfo.assetsList[i].amount', +this.miningInfo.assetsList[i].amount)
        console.log('+resp.result ', (+resp.result || (+this.miningInfo.assetsList[i].tokenType === 1 ? 0.15 : 0)))
        this.myBalance += (+resp.result || (+this.miningInfo.assetsList[i].tokenType === 1 ? 0.15 : 0)) * +this.miningInfo.assetsList[i].amount
        console.log('this.myBalance', this.myBalance)
        tokenPrice[this.miningInfo.assetsList[i].name] = resp.result || (+this.miningInfo.assetsList[i].tokenType === 1 ? 0.15 : 0)
      }
      for (let i = 0; i < this.miningInfo.supportTokenList.length; i++) {
        const resp = await routerObj.getAmountsIn(
          this.miningInfo.supportTokenList[i].tokenAddress
        )
        tokenPrice[this.miningInfo.supportTokenList[i].tokenName] = resp.result
      }
      localStorage.setItem('tokenPrice', JSON.stringify(tokenPrice))
    }
  },
  computed: {
    ...mapState(['lang'])
  },
  mounted () {
    this.init()
  }
}
</script>

