<template>
  <div class="home-wrap">
    <!-- 切换语言弹窗 -->
    <ChangeLangPop v-if="langShow" @close="handleLangShow"></ChangeLangPop>
    <!-- 分享弹窗 -->
    <SharePop
      v-if="shareShow"
      @close="handleShareShow"
      @showShop="handleShopShow"
    ></SharePop>
    <!-- 商店弹窗 -->
    <ShopPop v-if="shopShow" @close="handleShopShow" :type="shopType"></ShopPop>
    <!-- 添加饱食度 -->
    <AddSatiety
      v-if="satietyShow"
      @close="handleSatietyShow"
      :myInfo="myInfo"
    ></AddSatiety>
    <!-- 探险 -->
    <ExplorePop
      v-if="exploreShow"
      @close="handleExploreShow"
      @travelStart="handleTravelStart"
    ></ExplorePop>
    <!-- 探险状态 -->
    <ExploreStatusPop
      v-if="exploreStatusShow"
      @close="handleExploreStatusShow"
      :travelInfo="travelInfo"
      :exploreInfo="exploreInfo"
      :startExploreFlag="startExploreFlag"
      @changeStartFlag="changeStartFlag"
    ></ExploreStatusPop>
    <!-- 顶部 -->
    <Header
      ref="header"
      :myAccount="myAccount"
      :myInfo="myInfo"
      @show="handlePop"
      @musicControl="musicControl"
    />
    <!-- 页面布局 -->
    <HomeLayout
      @show="handlePop"
      :myInfo="myInfo"
      :travelInfo="travelInfo"
      @takeMiner="handleTakeMiner"
    />
    <CommomPop @close="handleActiveShow" v-if="activeShow">
      <div class="hungry" translate="no">
        {{ $t('home.yours') }}{{ myInfo.nickName }}{{ $t('home.hungry') }},{{
          $t('home.needPay')
        }}<span v-if="+myInfo.type === 1">50USDT</span>
        <span v-else>10USDT</span>
        {{ $t('home.wakeUp') }}
      </div>
      <div class="button" @click="handlePay">{{ $t('home.pay') }}</div>
    </CommomPop>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import HomeLayout from './components/HomeLayout.vue'
import { mapState, mapActions } from 'vuex'
import ChangeLangPop from './components/ChangeLangPop.vue'
import SharePop from './components/SharePop.vue'
import ShopPop from './components/ShopPop.vue'
import AddSatiety from './components/AddSatiety.vue'
import ExplorePop from '../explore/ExplorePop.vue'
import ExploreStatusPop from '../explore/ExploreStatusPop.vue'
import { getWalletInfo } from '@/services/user'
import { handleExploreStatus, handleExploreInfo } from '@/services/explore'
import { handleActive } from '@/services/home'
import CommomPop from '../../components/common/CommomPop.vue'
export default {
  name: '',
  components: { Header, HomeLayout, ChangeLangPop, SharePop, ShopPop, AddSatiety, ExplorePop, ExploreStatusPop, CommomPop },
  inject: ['reload'],
  data () {
    return {
      shopType: 0,
      travelInfo: {
        isTravel: 0, // 是否出行 0 未出行 1 已出行
        duration: null // 出行时间
      }, // 出行信息
      langShow: false,
      shareShow: false,
      shopShow: false,
      satietyShow: false,
      exploreShow: false,
      exploreStatusShow: false,
      isPop: 0,
      validTime: 0, // 探险时间
      myInfo: {
        level: 0,
        fragments: 0,
        nickName: '',
        experience: 0,
        isTravel: false,
        satiety: 0,
        type: '' // 卡类型 0普通 1团长 2机构 3媒体
      },
      exploreInfo: {}, // 探险详情信息
      startExploreFlag: false, // 开始出行 true---开始出行
      activeShow: false
    }
  },
  computed: {
    ...mapState(['myAccount']),
    common () {
      return this.$t('common')
    }
  },
  methods: {
    ...mapActions(['changeMyInfo']),
    async init () {
      this.$emit('changeLoading', true)
      this.$emit('changeLoadingIconType', 1)
      await this.getHomeInfo()
      // 只有普通nft才可以出行，需要获取出行信息(在出行的时候)
      if (this.myInfo.level && !this.myInfo.type && this.myInfo.isTravel) {
        this.getExploreInfo()
        // this.handleExploreStatus()
      }
    },
    handleActiveShow () {
      this.activeShow = !this.activeShow
    },
    handleShopShow (key) {
      this.shopType = key || 0
      this.shopShow = !this.shopShow
      if (!this.shopShow) {
        this.$refs.header.getMining()
      }
    },
    handleShareShow () {
      this.shareShow = !this.shareShow
    },
    handleLangShow () {
      this.langShow = !this.langShow
    },
    handleSatietyShow () {
      this.satietyShow = !this.satietyShow
    },
    handleExploreShow () {
      this.exploreShow = !this.exploreShow
    },
    judgeExploreShow () {
      if (this.travelInfo.isTraveling) {
        this.handleExploreStatusShow()
      } else if (!this.travelInfo.isTraveling && this.isPop) {
        this.exploreShow = !this.exploreShow
      } else {
        this.handleExploreStatusShow()
      }
    },
    handlePop (key) {
      switch (key) {
        case 'share':
          this.handleShareShow()
          break
        case 'lang':
          this.handleLangShow()
          break
        case 'satiety':
          if (+this.myInfo.satiety <= 0 && this.myInfo.activeCarId) {
            return this.handleActiveShow()
          }
          this.handleSatietyShow()
          break
        case 'explore':
          if (+this.myInfo.satiety <= 0 && this.myInfo.activeCarId) {
            return this.handleActiveShow()
          }
          this.getExploreStatus()
          break
        case 'shop':
          if (+this.myInfo.satiety <= 0 && this.myInfo.activeCarId) {
            return this.handleActiveShow()
          }
          this.handleShopShow()
          break
        case 'active':
          this.handleActiveShow()
      }
    },
    // 获取出行信息已读未读消息
    async getExploreStatus () {
      const resp = await handleExploreInfo()
      if (!resp.success) return
      this.isPop = resp.data.isPop
      this.exploreInfo = resp.data
      // this.exploreInfo.estimatedEndTime = this.$moment(this.exploreInfo.estimatedEndTime).valueOf()
      // const nowTime = this.$moment().format('x')
      // this.exploreInfo.validTime = (this.exploreInfo.estimatedEndTime - nowTime) / 1000
      // if (this.exploreInfo.validTime < 0) {
      //   this.exploreInfo.travelTime = (this.exploreInfo.estimatedEndTime - this.$moment(this.exploreInfo.startTime).valueOf()) / 1000
      // }
      this.judgeExploreShow()
    },
    handleExploreStatusShow () {
      this.exploreStatusShow = !this.exploreStatusShow
    },
    // 获取首页信息
    async getHomeInfo () {
      const resp = await getWalletInfo()
      if (!resp.success) return
      this.myInfo = resp.data
      if (this.myInfo.isBinding === 0) {
        return this.$emit('connect')
      }
      if (+this.myInfo.satiety <= 0 && this.myInfo.activeCarId) {
        return this.handleActiveShow()
      }
      this.changeMyInfo(this.myInfo)
    },
    // 获取出行信息
    async getExploreInfo () {
      const resp = await handleExploreStatus()
      if (!resp.success) return
      this.travelInfo = resp.data
    },
    // 开始探险
    async handleTravelStart () {
      this.startExploreFlag = true
      this.handleExploreShow()
      await this.getExploreInfo()
      this.getExploreStatus()
      this.getHomeInfo()
    },
    // 首次探险状态修改
    changeStartFlag () {
      this.startExploreFlag = false
    },
    // 音乐控制
    musicControl (flag) {
      this.$emit('musicControl', flag)
    },
    async handlePay () {
      const resp = await handleActive()
      if (!resp.success) return
      // this.$refs.header.myBalance -= 100 * JSON.parse(localStorage.getItem('tokenPrice')).SFO
      this.activeShow = false
      this.$toast('Success')
      this.reload()
      // this.init()
    },
    handleTakeMiner (item) {
      this.$refs.header.myBalance += item * (JSON.parse(localStorage.getItem('tokenPrice')).SFO || 0.15)
      console.log('this.$refs.header.myBalance ', this.$refs.header.myBalance)
    }
  },
  mounted () {
    this.init()
  }
}
</script>

<style scoped lang="scss">
.home-wrap {
  background-color: #1d1d1d;
  font-size: 12px;
  width: 100%;
  .hungry {
    margin-top: 20px;
    color: #fff;
    font-size: 14px;
    span {
      color: #ff2600;
    }
  }
  .button {
    height: 40px;
    margin: 0 auto;
    width: 70%;
    line-height: 40px;
    background-color: #faba5c;
    border-radius: 5px;
    font-size: 14px;
    margin-top: 20px;
    font-weight: 600;
  }
}
</style>
