import request from '@/utils/request'

// 分享页面
export const handleSharePage = (params) => {
  return request('GET', '/dapp/wallet/share', params, true, true)
}

// 分享页面
export const queryMineral = (params) => {
  return request('GET', '/dapp/goldOre/list', params, true, true, 0)
}

// 分享页面
export const queryGetMineral = (params) => {
  return request('GET', '/dapp/goldOre/charge', params)
}

// 激活
export const handleActive = (params) => {
  return request('POST', '/dapp/wallet/satietyActivation', params)
}
