<script>
import { queryMineral, queryGetMineral } from '@/services/home'

export default {
  name: '',
  props: ['myInfo', 'travelInfo'],
  data () {
    return {
      level: 1,
      imgArray: {
        consult: [
          require('../../../assets/home/consult-1.png'),
          require('../../../assets/home/consult-2.png'),
          require('../../../assets/home/consult-3.png'),
          require('../../../assets/home/consult-4.png'),
          require('../../../assets/home/consult-4.png')
        ],
        explore: [
          require('../../../assets/home/explore-1.png'),
          require('../../../assets/home/explore-2.png'),
          require('../../../assets/home/explore-3.png'),
          require('../../../assets/home/explore-4.png'),
          require('../../../assets/home/explore-4.png')
        ],
        shop: [
          require('../../../assets/home/shop-1.png'),
          require('../../../assets/home/shop-2.png'),
          require('../../../assets/home/shop-3.png'),
          require('../../../assets/home/shop-4.png'),
          require('../../../assets/home/shop-4.png')
        ],
        tool: [
          require('../../../assets/home/tool-1.png'),
          require('../../../assets/home/tool-2.png'),
          require('../../../assets/home/tool-3.png'),
          require('../../../assets/home/tool-4.png'),
          require('../../../assets/home/tool-4.png')
        ],
        grass: [require('../../../assets/home/grass-1.png')],
        starImg: [
          require('../../../assets/star/main/main.png'),
          require('../../../assets/star/main/main.png'),
          require('../../../assets/star/main/main1.png'),
          require('../../../assets/star/main/main2.png'),
          require('../../../assets/star/main/main3.png'),
          require('../../../assets/star/main/main4.png')
        ],
        coinArr: [
          require('../../../assets/home/coin/coin1.png'),
          require('../../../assets/home/coin/coin2.png'),
          require('../../../assets/home/coin/coin3.png'),
          require('../../../assets/home/coin/coin4.png'),
          require('../../../assets/home/coin/coin5.png'),
          require('../../../assets/home/coin/coin6.png')
        ]
      },
      consultImg: '',
      exploreImg: '',
      shopImg: '',
      toolImg: '',
      grassImg: '',
      starImg: '',
      coinPosition: [],
      coinPosition1: [
        [310, 140], [230, 160], [140, 180], [50, 70], [140, 90], [160, 250], [50, 300], [80, 400], [180, 440], [280, 470], [300, 580], [200, 520]
      ],
      mineralList: []
    }
  },
  methods: {
    init () {
      this.level = this.myInfo.level || 0
      this.getImg()
      this.getMineral()
    },
    getImg () {
      this.consultImg = this.imgArray.consult[this.level] ? this.imgArray.consult[this.level] : this.imgArray.consult[4]
      this.exploreImg = this.imgArray.explore[this.level] ? this.imgArray.explore[this.level] : this.imgArray.explore[4]
      this.shopImg = this.imgArray.shop[this.level] ? this.imgArray.shop[this.level] : this.imgArray.shop[4]
      this.toolImg = this.imgArray.tool[this.level] ? this.imgArray.tool[this.level] : this.imgArray.tool[4]
      this.grassImg = this.imgArray.grass[0]
      this.starImg = this.imgArray.starImg[this.level]
      if (this.myInfo.type) {
        this.starImg = this.$gbUtils.getRoleImg(this.myInfo.type)
      }
    },
    handleShow (key) {
      if (!this.myInfo.level) return
      // 机构、媒体、团队长无法探险
      if (key === 'explore' && this.myInfo.type) { return this.$toast(this.$t('home.notGo')) }
      this.$emit('show', key)
    },
    handleToPage (path) {
      if (+this.myInfo.satiety <= 0 && this.myInfo.activeCarId) {
        return this.$emit('show', 'active')
      }
      if (!this.myInfo.level) return
      this.$gbUtils.handleToPage(path)
    },
    // 获取矿石
    async getMineral () {
      console.log(this.coinPosition1)
      this.coinPosition1 = this.$gbUtils.shuffle(this.coinPosition1)
      console.log(this.coinPosition1)

      this.mineralList = []
      const resp = await queryMineral({
        pageSize: 12,
        pageNum: 1,
        status: 0
      })
      if (!resp.success) return
      this.mimerNumber = resp.rows.length
      this.mineralList = resp.rows
    },
    // 点击矿石
    async handleMineral (item, index) {
      this.$refs['item' + index][0].style.pointerEvents = 'none'
      const resp = await queryGetMineral({
        id: item.id
      })
      if (!resp.success) return
      this.mineralDisappearance(index)
      this.$emit('takeMiner', item.amount)
      this.mimerNumber--
      if (this.mimerNumber === 0) {
        this.getMineral()
      }
    },
    // 矿石消失
    mineralDisappearance (index) {
      this.$refs['item' + index][0].style.opacity = 0
      this.$refs['item' + index][0].style.top =
        parseInt(this.$refs['item' + index][0].style.top) + 10 + 'px'
      // this.$refs['item' + index][0].style.pointerEvents = 'none'
    },
    timeStamp (StatusMinute) {
      var day = parseInt(StatusMinute / 60 / 24)
      var hour = parseInt((StatusMinute / 60) % 24)
      var min = parseInt(StatusMinute % 60)
      StatusMinute = ''
      if (day > 0) {
        StatusMinute = day + this.$t('common.day')
      }
      if (hour > 0) {
        StatusMinute += hour + this.$t('common.hour')
      }
      if (min >= 0) {
        StatusMinute += parseFloat(min) + this.$t('common.min')
      }
      return StatusMinute
    }
  },
  mounted () {
    this.init()
  },
  watch: {
    myInfo (val) {
      this.level = val.level || 0
      this.getImg()
    }
  }
}
</script>

