<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      max-width: 400px;
      background-color: #000;
      padding: 20px 30px;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 1);
      border-radius: 4px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="lang-wrap" translate="no">
      <div class="title">{{ $t('home.changeLang') }}</div>
      <div
        class="lang-item"
        @click="changeLanguage('EN')"
        :class="{ active: lang === 'EN' }"
      >
        English
      </div>
      <div
        class="lang-item"
        @click="changeLanguage('ZH')"
        :class="{ active: lang === 'ZH' }"
      >
        繁體中文
      </div>
    </div>
  </VanPopup>
</template>

<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: '',
  data () {
    return {
      show: true
    }
  },
  methods: {
    ...mapActions(['changeLang']),
    changeLanguage (lang) {
      this.changeLang(lang)
      this.$toast('Success')
      this.$parent.reload()
      this.closePop()
    },
    closePop () {
      this.$emit('close')
    }
  },
  computed: {
    ...mapState(['lang'])
  },
  mounted () {

  }
}
</script>

<style scoped lang="scss">
.lang-wrap {
  .title {
    color: #dbd9d5;
    text-align: center;
    margin-bottom: 30px;
  }
  .lang-item {
    margin-top: 10px;
    height: 41px;
    text-align: center;
    line-height: 41px;
    font-weight: 600;
    border-radius: 4px;
  }
  .lang-item {
    color: #fff;
    background: #352c25;
  }
  .active {
    color: #352c25;
    background: #dbd9d5;
  }
}
</style>
