import request from '@/utils/request'

// 食物列表
export const handleFoodList = (params) => {
  return request('GET', '/dapp/walletFood/list', params, true, true, 1)
}

// 购买食物
export const handleBuyFood = (params) => {
  return request('POST', '/dapp/foodShop/placeOrder', params, true, true, 0)
}

// 喂食物
export const handleEatFood = (params) => {
  return request('POST', '/dapp/walletFood/eat', params, true, true, 0)
}
