<template>
  <VanPopup
    style="box-sizing: border-box; background-color: transparent"
    round
    v-model="show"
    :close-on-click-overlay="false"
  >
    <div class="ignore">
      <div class="share-wrap">
        <div class="close-wrap">
          <div class="close-icon" @click="handleShowNotice">
            <svg class="icon1" aria-hidden="true">
              <use xlink:href="#jieshao"></use>
            </svg>
          </div>
          <div class="close-icon" @click="closePop">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#a-guanbixiao1"></use>
            </svg>
          </div>
        </div>
        <div class="title">{{ $t('explore.addSatiety') }}</div>
        <div class="food-wrap" translate="no">
          <SatietyFoodItem
            v-for="item in foodList"
            :key="item.id"
            class="item"
            :dataSource="item"
            @show="handleLightPop"
          ></SatietyFoodItem>
          <div class="none-data" v-if="foodList.length < 1">
            {{ $t('common.noHave') }}
          </div>
        </div>
      </div>
    </div>
    <AddSatiety v-if="noticeShow" @close="handleShowNotice"></AddSatiety>
    <LightPop v-if="lightPopShow" @close="handleLightPop">
      <div class="pop-wrap">
        <div class="des1">{{ tempData.introduce }}</div>
        <div class="des2">
          {{ $t('explore.canAdd') }}<span> {{ tempData.energy }} </span
          >{{ $t('explore.satiety') }}
        </div>
        <div class="select-wrap" @click="handleEatFood">
          {{ $t('common.confirm') }}
        </div>
      </div>
    </LightPop>
  </VanPopup>
</template>

<script>
import { handleFoodList, handleEatFood } from '@/services/food'
import AddSatiety from './SatietyNoticePop.vue'
import SatietyFoodItem from './SatietyFoodItem.vue'
import LightPop from '../../../components/LightPop.vue'
export default {
  name: '',
  props: ['myInfo'],
  data () {
    return {
      noticeShow: false,
      lightPopShow: false,
      show: true,
      tempData: {},
      foodList: []
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    handleLightPop (data) {
      if (parseFloat(this.myInfo.satiety) >= this.$gbUtils.getFullSatiety(this.myInfo)) return this.$toast('饱食度已达上限，无法继续增加')
      this.tempData = data
      this.lightPopShow = !this.lightPopShow
    },
    handleOverLay () {
    },
    handleShowNotice () {
      this.noticeShow = !this.noticeShow
    },
    async handleEatFood () {
      const resp = await handleEatFood({
        id: this.tempData.id
      })
      if (!resp.success) return
      this.getList()
      this.$parent.getHomeInfo()
      this.lightPopShow = false
    },
    async getList () {
      const resp = await handleFoodList()
      if (!resp.success) return
      this.foodList = resp.rows
      this.foodList.forEach(item => {
        item.json = JSON.parse(item.json)
      })
    }
  },
  mounted () {
    this.getList()
  },
  components: { AddSatiety, SatietyFoodItem, LightPop }
}
</script>

